<template>
  <div class="video-interaction-container">
    <el-row gutter={20}>
      <el-col :span="18">
        <!-- 视频播放区 -->
        <div class="video-player" ref="videoPlayer">
          <video ref="videoRef" width="100%" controls>
            <source :src="videoSrc" type="video/mp4">
            您的浏览器不支持 Video 标签。
          </video>
          <!-- 弹幕显示区 -->
          <div class="danmu-display">
            <div v-for="dm in danmus" :key="dm.id" :style="getDanmuStyle(dm)" class="danmu-item">
              {{ dm.text }}
            </div>
          </div>
        </div>
        <!-- 视频控制区 -->
        <div class="video-controls">
          <el-button-group>
            <el-button icon="el-icon-thumb" @click="toggleLike">点赞{{ likes }}</el-button>
            <el-button icon="el-icon-money" @click="coin">投币</el-button>
            <el-button icon="el-icon-share" @click="share">转发</el-button>
          </el-button-group>
          <el-input v-model="newDanmu" placeholder="输入弹幕..." class="danmu-input"></el-input>
          <el-button type="primary" @click="postDanmu">发送弹幕</el-button>
        </div>
        <!-- 评论区 -->
        <div class="comments">
          <el-input v-model="newComment" placeholder="写下你的评论..."></el-input>
          <el-button type="primary" @click="postComment">发送</el-button>
          <div class="comment-list">
            <div v-for="comment in comments" :key="comment.id" class="comment">
              {{ comment.text }}
            </div>
          </div>
        </div>
      </el-col>
      <el-col :span="6">
        <!-- 右侧栏 -->
        <div class="sidebar">
          <div class="danmu">
            <el-switch v-model="danmuEnabled" active-text="弹幕开"></el-switch>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      likes: 0,
      newComment: '',
      comments: [],
      danmus: [],
      newDanmu: '',
      danmuEnabled: true,
      videoSrc: require('@/assets/videos/1.mp4')
    };
  },
  methods: {
    toggleLike() {
      this.likes++;
    },
    coin() {
      this.$message('投币成功！');
    },
    share() {
      this.$message('分享成功！');
    },
    postComment() {
      if (this.newComment.trim() !== '') {
        this.comments.push({id: Date.now(), text: this.newComment});
        this.newComment = '';
      }
    },
    postDanmu() {
      if (this.newDanmu.trim() !== '') {
        const danmu = {id: Date.now(), text: this.newDanmu};
        console.log('Posting danmu:', danmu);
        this.danmus.push(danmu);
        this.newDanmu = '';
        console.log('Current danmus:', this.danmus);
        // 删除弹幕延时
        setTimeout(() => {
          this.removeDanmu(danmu.id);
        }, this.getDanmuDuration());
      }
    },
    removeDanmu(id) {
      console.log('Removing danmu with id:', id);
      this.danmus = this.danmus.filter(dm => dm.id !== id);
      console.log('Current danmus after removal:', this.danmus);
    },
    getDanmuStyle() {
      const duration = this.getDanmuDuration() / 1000; // 转换为秒
      const style = {
        animation: `danmu-move-8ef35988 ${duration}s linear`,
        left: `100%`, // 弹幕从视频的最右侧开始
        top: `${Math.random() * 90}%`, // 随机位置出现在视频区域的顶部
      };
      console.log('Danmu style:', style);
      return style;
    },


    getTextWidth(text) {
      const canvas = document.createElement('canvas');
      const context = canvas.getContext('2d');
      context.font = '20px Arial'; // 假设弹幕使用20px Arial字体
      const metrics = context.measureText(text);
      console.log('Text width for', text, ':', metrics.width);
      return metrics.width;
    },
    getDanmuDuration() {
      const videoPlayerWidth = this.$refs.videoPlayer.clientWidth;
      const speed = 100; // 每秒移动100px
      const duration = (videoPlayerWidth / speed) * 1000; // 持续时间（毫秒）
      console.log('Danmu duration:', duration);
      return duration;
    }
  }
};
</script>

<style scoped>
.video-interaction-container {
  padding: 20px;
}

.video-player {
  position: relative;
}

.video-controls {
  display: flex;
  align-items: center;
  margin-top: 10px;
}

.danmu-input {
  margin-left: 10px;
  flex: 1;
}

.comments {
  margin-top: 20px;
}

.comment-list {
  margin-top: 10px;
}

.comment {
  padding: 5px;
  border-bottom: 1px solid #ccc;
}

.sidebar {
  padding: 20px;
  border-left: 1px solid #eee;
}

.danmu-display {
  z-index: 100;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  overflow: hidden;
}

.danmu-item {
  position: absolute;
  white-space: nowrap;
  color: white;
  font-size: 20px;
}

@keyframes danmu-move {
  from {
    transform: translateX(0%); /* 从右边界开始 */
    opacity: 1;
  }
  to {
    transform: translateX(-100vw); /* 到左边界外结束 */
    opacity: 0;
  }
}

</style>
